.contents-index-list {
	// block.scss
	display: table;
	width: 100%;
	margin-bottom: 20px;
	padding-bottom: 20px;
	border-bottom: 1px dotted colors(col-lightgray);
	text-decoration: none;
	table-layout: fixed;
	&:last-child {
		margin-bottom: 0;
		padding-bottom: 0;
		border-bottom: none;
	}
	&-img {
		@include table-cell;
		width: 200px;
		vertical-align: top;
		overflow: hidden;
		img {
			width: auto;
			max-width: calc(100% - 2px);
			height: auto;
			border: 1px solid colors(link);
			:visited & {
				border: 1px solid colors(visited);
			}
		}
	}
	&-body {
		@include table-cell;
		@include padding(10px null null 10px);
		vertical-align: top;
		&-ttl {
			display: block;
			margin-bottom: 10px;
			color: colors(ttl);
			font-weight: 700;
			.qa-type & {
				position: relative;
				@include padding(null null null 40px);
				&:before {
					@include inline-block;
					position: absolute;
					left: 0;
					top: 0;
					width: 30px;
					height: 30px;
					@include border-radius(50%,50%,50%,50%); 
					background-color: colors(ttl);
					color: colors(col-white);
					@include fz(20);
					font-weight: 700;
					line-height: 30px;
					text-align: center;
					content: "Q";
				}
			}
		}
		&-txt {
			display: block;
			min-height: 3em;
			color: colors(basetxt);
			@include fz(14);
			.qa-type & {
				position: relative;
				@include padding(null null null 40px);
				&:before {
					@include inline-block;
					position: absolute;
					left: 0;
					top: 0;
					width: 30px;
					height: 30px;
					@include border-radius(50%,50%,50%,50%); 
					background-color: colors(col-gray);
					color: colors(col-white);
					@include fz(20);
					font-weight: 700;
					line-height: 30px;
					text-align: center;
					vertical-align: middle;
					content: "A";
				}
			}
		}
		&-link {
			display: block;
			@include fz(14);
			text-align: right;
			text-decoration: underline;
		}
	}
}

