//=========================

.under-arw-list {
	&-area {
		margin-bottom: 20px;
		counter-reset: under-arw-list;
		li:not(:last-child) {
			position: relative;
			padding-bottom: 50px;
			&:before {
				position: absolute;
				bottom: 25px;
				left: 50%;
				width: 40px;
				height: 15px;
				background-color: colors(col-gray);
				line-height: 1;
				@include translate(-50%,0);
				content: "";
			}
			&:after {
				position: absolute;
				bottom: 10px;
				left: 50%;
				border: 40px solid transparent;
				border-top: 15px solid colors(col-gray);
				border-bottom: 0 solid transparent;
				@include translate(-50%,0);
				content: "";
			}
		}
		.list-content-inner {
			display: block;
			position: relative;
			@include padding(10px 10px 10px 25px);
			border: 1px solid colors(col-lightgray);
			&:before {
				position: absolute;
				left: 10px;
				top: 10px;
				counter-increment: under-arw-list;
				content: counter(under-arw-list);
			}
		}
	}
}

