.type-checkbox {
	&-area {
		margin-bottom: 20px;
	}
	&-list {
		position: relative;
		margin-bottom: 10px;
		padding-left: 26px;
		&:before {
			position: absolute;
			top: 2px;
			left: 0;
			width: 20px;
			height: 20px;
			border: 2px solid colors(disable);
			background-color: colors(col-white);
			content: "";
		}
		&:after {
			position: absolute;
			top: 6px;
			left: 3px;
			width: 14px;
			height: 6px;
			border-left: 4px solid colors(link);
			border-bottom: 4px solid colors(link);
			@include rotate(-45);
			content: "";
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
}