// styletitle.scss

.news-page {
	p {
		@include margin(20px null 20px null);
		&:first-of-type {
			margin-top: 0;
		}
	}
	.ttl-section + p {
		margin-top: 0;
	}
}

// 
//------------------------------------------------------------

.next-content {
	@include margin(30px null 30px null);
	text-align: center;
	a {
		&:before {
			@include inline-block;
			@include margin(null 5px null 5px);
			color: colors(col-lightgray);
			text-decoration: none;
			content: "|"
		}
	}
}

// 
//------------------------------------------------------------

.news-supply-link {
	text-align: right;
	@include margin(30px null 30px null);
	img {
		vertical-align: baseline;
	}
}