//------------------------------------------------------------
// color variavle setting
//------------------------------------------------------------

$black: rgba(0,0,0,1); // #000000

//=========================
// 
//=========================

$colors: (
	basetxt: lighten($black, 20%), // 
	link: rgba(107,150,250,1), // 
	visited: rgba(78,23,173,1), // #4e17ad 
	ttl: rgba(52,103,224,1), // #3467e0 
	disable: lighten($black, 80%), // #cccccc 
	alert: rgba(255,0,0,1), // #ff0000 
	col-red: rgba(230,23,23,1), // #e61717 
	col-black: rgba(0,0,0,1), // #000000 
	col-blue: rgba(74,103,173,1), // #4a67ad 
	col-orange: rgba(253,128,37,1), // #fd8025 
	col-gray: lighten($black, 40%), // #666666 
	col-lightgray: lighten($black, 60%), // #999999 
	col-xlightgray: lighten($black, 90%), // #e6e6e6 
	col-white: rgba(255,255,255,1), // #ffffff 
	col-yellow: rgba(255,221,51,1), // #ffdd33 
	col-lightyellow: rgba(255,247,204,1.0) // #fff7cc 
);

@function colors($key) {
	@return map-get($colors, $key);
}