//------------------------------------------------------------
// TOP
//------------------------------------------------------------

//=========================
// 
//=========================

.main-content-atrwork {
	text-align: center;
	@include margin(20px null 20px null);
	img {
		@include inline-block;
		@include margin(10px null 10px null);
		@include break-point(tblandscape) {
			width: auto;
			max-width: 100%;
			height: auto;
			max-height: 100%;
		}
		&:first-child {
			margin-top: 0px;
		}
		&:last-child {
			margin-bottom: 0px;
		}
	}
}

// 
//------------------------------------------------------------

.content-strong-bg {
	background-color: colors(col-lightyellow);
	&.atrwork-cap {
		margin-top: 10px;
		padding: 5px;
		@include fz(20);
		text-align: center;
	}
}

//=========================
// 
//=========================

.main-content-tbl {
	width: 100%;
	margin-bottom: 20px;
	// 
	tr {
		border-bottom: 1px dotted colors(col-lightgray);
	}
	th, td {
		padding: 15px;
		border-right: 1px dotted colors(col-lightgray);
		vertical-align: top;
		box-sizing: border-box;
		&:not(.content-strong-bg){
			background-color: colors(col-white);
		}
		&:last-child {
			border-right: none;
		}
	}
	th {
		text-align: center;
	}
	thead {
		th, td {
			vertical-align: middle;
			img {
				@include inline-block;
				vertical-align: middle;
			}
		}
	}
	tbody {
		tr {
			&:last-child {
				border-bottom: none;
			}
		}
	}
	// 2
	&.two-column {
		th, td {
			width: 50%;
			&:first-child {
				width: auto;
			}
		}
		// 2
		&.pair {
			th, td {
				&:not(:first-child) {
					width: 72%;
				}
			}
		}
	}
	// 3
	&.three-column {
		th, td {
			width: 33%;
			&:first-child {
				width: auto;
			}
		}
		// 3
		&.compare {
			th, td {
				&:not(:first-child) {
					width: 36%;
				}
			}
		}
	}
	// 
	&.transportation-list {
		thead {
			th {
				color: colors(col-blue);
			}
		}
		tbody {
			th {
				width: 25%;
			}
			td {
				&:last-child {
					width: 20%;
					text-align: center;
				}
			}
		}
	}
}

//=========================
// 
//=========================

.purchase-explain-txt {
	@include inline-block;
	width: 50%;
	padding-right: 10px;
	@include fz(14);
	vertical-align: top;
	box-sizing: border-box;
	&-td {
		@include fz(0);
	}
}

// 
//------------------------------------------------------------

.specialty-point {
	@include inline-block;
	width: 50%;
	@include padding(5px 10px 5px 10px);
	@include border-radius(3px,3px,3px,3px);
	border: 1px solid colors(col-blue);
	@include fz(14);
	vertical-align: top;
	box-sizing: border-box;
	&-ttl {
		color: colors(ttl);
		font-weight: 700;
		text-align: center;
	}
	&-body {
		text-align: left;
	}
}

//=========================
// 
//=========================

.blue-disc-num {
	&-area {
		counter-reset: blue-disc-num;
	}
	&-list {
		position: relative;
		@include margin(null null 20px 50px);
		@include fz(16);
		font-weight: 700;
		&:last-child {
			margin-bottom: 0;
		}
		&:before {
			@include inline-block;
			position: absolute;
			top: 0;
			left: -40px;
			width: 30px;
			height: 30px;
			@include border-radius(50%,50%,50%,50%);
			background-color: colors(ttl);
			color: colors(col-white);
			@include fz(20);
			font-weight: 700;
			line-height: 30px;
			text-align: center;
			vertical-align: middle;
			counter-increment: blue-disc-num;
			content: counter(blue-disc-num);
		}
	}
}

//=========================
// 
//=========================

.pickup-review {
	&-area {
		position: relative;
	}
	&-block {
		position: relative;
	}
	&-list {
		padding: 20px;
		border: 4px solid colors(col-yellow);
		border-bottom: none;
		box-sizing: border-box;
		&:last-child {
			border-bottom: 4px solid colors(col-yellow);
		}
		&-ttl {
			@extend .assessment-example-car-ttl;
			position: static;
			left: 0;
			width: auto;
			@include translate(0%,0%);
		}
		&-body {
			display: table;
			width: 100%;
			table-layout: fixed;
			figure {
				@include table-cell;
				width: 360px;
				height: 180px;
				text-align: center;
				vertical-align: middle;
				img {
					position: relative;
					width: auto;
					max-width: 100%;
					height: auto;
					max-height: 100%;
				}
			}
		}
		&-info {
			@include table-cell;
			vertical-align: top;
			&-ttl {
				color: colors(col-blue);
				font-weight: 700;
			}
			// .example-car-info-group_block.scss
		}
		&-eval {
			&-box {
				@extend .mv-review-eval-box;
				&-ttl {
					@extend .mv-review-eval-box-ttl;
				}
				&-body {
					@extend .mv-review-eval-box-body;
				}
				// .price-group-tbl, .price-group-tbl, .difference-group_block.scss
			}
		}
	}
}