.mv-three-column-img {
	margin-bottom: 20px;
	@include fz(0);
	text-align: center;
	img {
		@include inline-block;
		margin-right: 30px;
		@include break-point(tblandscape) {
			width: calc(33% - 10px);
			height: auto;
			margin-right: 10px;
		}
		&:last-child {
			margin-right: 0;
		}
	}
}

//=========================
// point
//=========================

.point-balloon {
	@include inline-block;
	position: relative;
	margin-bottom: 6px;
	padding: 5px 10px;
	@include border-radius(3px,3px,3px,3px);
	background-color: colors(col-blue);
	color: colors(col-white);
	@include fz(20);
	font-weight: 700;
	line-height: 1;
	text-align: center;
	&:after {
		position: absolute;
		right: 10px;
		bottom: -12px;
		@include arrow(bottom,12,colors(col-blue));
		content: "";
	}
}

