.index {
	&-area {
		margin: 0 0 10px;
		padding: 10px;
		background-color: rgba(colors(col-xlightgray),0.35);
		@include fz(0);
		text-align: center;
	}
	&-list {
		@include inline-block;
		margin-right: 10px;
		padding-right: 10px;
		border-right: 1px solid colors(col-lightgray);
		@include fz(14);
		line-height: 1;
		@include break-point(tbportrait) {
			@include margin(5px null 5px null);
		};
		&:last-child {
			margin-right: 0;
			padding-right: 0;
			border-right: none;
		}
		& a {
			font-weight: 700;
		}
	}
}

// 
//------------------------------------------------------------

.merchant-name {
	&-area {
		@include fz(0);
		margin-bottom: 10px;
		@include break-point(tbportrait) {
			@include flex-block;
			-webkit-flex-wrap: wrap;
			flex-wrap: wrap;
		};
	}
	&-list {
		@include inline-block;
		position: relative;
		width: calc(50% - 10px);
		margin-right: 10px;
		margin-bottom: 10px;
		@include fz(16);
		vertical-align: top;
		&:nth-child(2n) {
			width: 50%;
			margin-right: 0;
		}
		a {
			display: block;
			@include padding(5px 5px 5px 10px);
			&:before {
				@include arw-wedge;
				position: absolute;
				top: 1.6rem;
				left: 0;
				content: "";
			}
			&:hover {
				@include block-link;
			}
		}
	}
}

