// mv-review-list-area

.mv-shop-outline {
	&-area {
		@extend .mv-review-list-area;
	}
	&-block {
		@extend .mv-review-main-info-block;
	}
	&-score-box {
		@extend .mv-review-spec-box;
		width: auto;
		@include break-point(tbportrait) {
			margin-bottom: 20px;
		}
		figure {
			width: 200px;
			height: 120px;
			padding: 10px;
			vertical-align: top;
			box-sizing: border-box;
			// .mv-shop-outline-score-box figure img extend
			@include break-point(tbportrait) {
				margin-bottom: 20px;
			}
		}
	}
	&-group {
		@include inline-block;
		margin-left: 15px;
		vertical-align: top;
	}
	&-ttl {
		margin-bottom: 5px;
		color: colors(ttl);
		@include fz(18);
		font-weight: 700;
		.review-num {
			color: colors(basetxt);
			font-weight: normal;
		}
	}
	&-list {
		@extend .review-spec-list;
		// .mv-shop-outline-list.satisfy extend
		&-ttl {
			@extend .mv-review-spec-list-ttl;
			width: 120px;
			// .satisfy .mv-shop-outline-list-ttl extend
		}
		&-body {
			@extend .mv-review-spec-list-body;
			@include fz(18);
			// .satisfy .mv-shop-outline-list-body extend
			// .mv-shop-outline-list-body .review-star extend
		}
	}
	// 
	//------------------------------------------------------------
	&-message {
		@extend .review-outline-block;
		display: block;
		clear: both;
		margin-top: 20px;
		.message-ttl {
			@include fz(16);
			font-weight: 700;
		}
	}
	&-info {
		&-box {
			@extend .mv-review-spec-box;
		}
		&-tbl {
			width: 100%;
			tr {
				border-top: 1px dotted colors(col-lightgray);
			}
			th, td {
				padding-top: 15px;
				padding-bottom: 15px;
			}
			th {
				width: 110px;
				color: colors(col-blue);
				font-weight: 700;
			}
			td {
				padding-left: 10px;
				padding-right: 10px;
			}
		}
	}
}

//=========================
// 
//=========================

.shop-main-photo {
	&-area {
		width: 708px;
		margin-bottom: 20px;
		overflow: hidden;
		@include fz(0);
		@include break-point(tbportrait) {
			width: 100%;
		}
	}
	&-block {
		@include inline-block;
		width: 297px;
		margin-right: 10px;
		margin-bottom: 5px;
		padding: 20px;
		border: 1px solid colors(col-xlightgray);
		@include fz(14);
		@include box-shadow(0,2px,2px,0,rgba(colors(col-black),0.2));
		box-sizing: border-box;
		vertical-align: top;
		&:last-child {
			margin-right: 0;
		}
		figure {
			position: relative;
			width: 257px;
			height: 194px;
			margin-bottom: 10px;
			border: 1px solid colors(col-lightgray);
			box-sizing: border-box;
			overflow: hidden;
			img {
				position: relative;
				top: 50%;
				left: 50%;
				width: auto;
				max-width: 100%;
				height: auto;
				max-height: 100%;
				@include translate(-50%,-50%);
			}
		}
	}
	&-captxt {
		@include fz(12);
	}
}

//=========================
// 
//=========================

.shop-main-message {
	&-ttl {
		@include fz(18);
		font-weight: 700;
	}
}

