.sitemap-tree {
	&-area {
		margin-bottom: 10px;
		@include fz(0);
	}
	&-list {
		@include inline-block;
		min-width: calc(33% - 10px);
		@include margin(null 10px 10px null);
		@include fz(14);
		vertical-align: top;
		&:nth-child(3n) {
			margin-right: 0;
			@include break-point(tbportrait) {
				margin-right: 10px;
			}
		}
		a {
			&:link {
				display: block;
				padding: 10px 5px;
				border: 1px solid colors(link);
				text-decoration: none;
			}
			&:hover {
				@include block-link;
			}
			&:visited {
				border: 1px solid colors(visited);
			}
		}
	}
}