input.input-search-keyword {
	width: 300px;
	height: 36px;
	margin-right: 5px;
	vertical-align: top;
}

//=========================
// 
//=========================

.maker-search {
	&-area {
		@include fz(0);
	}
	&-list {
		@include inline-block;
		width: 33%;
		padding: 0 15px 10px 0;
		@include fz(16);
		vertical-align: top;
		box-sizing: border-box;
		&:last-child {
			margin-right: 0;
		}
		a {
			display: block;
			text-decoration: none;
			&:hover {
				@include block-link;
			}
		}
		figure {
			display: table;
			width: 100%;
			table-layout: fixed;
		}
		figcaption {
			@include table-cell;
			padding-left: 5px;
			text-decoration: underline;
			vertical-align: middle;
		}
	}
	&-img {
		@include table-cell;
		position: relative;
		width: 66px;
		height: 42px;
		text-align: center;
		vertical-align: middle;
		overflow: hidden;
		img {
			width: auto;
			max-width: 100%;
			height: auto;
			max-height: 100%;
		}
	}
}

//------------------------------------------------------------
//  / 
//------------------------------------------------------------

//=========================
// 
//=========================

.car-thumb {
	&-area {
		@include fz(0);
		margin-bottom: 10px;
		@include break-point(tblandscape) {
			@include flex-block;
			-webkit-flex-wrap: wrap;
			flex-wrap: wrap;
		};
		&.three-column .car-thumb-list {
			width: 230px;
			figure {
				width: 100%;
				height: 131px;
			}
			&:nth-child(3n) {
				margin-right: 0;
			}
			&:nth-child(4n), &:nth-child(5n) {
				margin-top: 20px;
			}
			&:nth-child(5n) {
				margin-right: 8px;
			}
		}
	}
	&-list {
		@include inline-block;
		width: 135px;
		margin: 20px 8px 0 0;
		@include fz(14);
		vertical-align: top;
		@include break-point(tblandscape) {
			width: calc(20% - 8px);
		};
		&:nth-child(5n) {
			margin-right: 0;
		}
		&:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(4), &:nth-child(5) {
			margin-top: 0;
		}
		a {
			display: block;
			text-decoration: none;
			&:hover {
				@include block-link;
			}
			&:visited figure {
				border: 1px solid colors(visited);
			}
		}
		figure {
			display: block;
			position: relative;
			width: 133px;
			height: 76px;
			padding: 10px;
			border: 1px solid colors(link);
			overflow: hidden;
			box-sizing: border-box;
			@include break-point(tblandscape) {
				width: 100%;
			};
			img {
				position: relative;
				top: 50%;
				left: 50%;
				width: auto;
				max-width: 100%;
				height: auto;
				max-height: 100%;
				@include translate(-50%,-50%);
			}
		}
	}
	&-ttl {
		display: block;
		padding-top: 5px;
		position: relative;
		.ico-rank {
			position: absolute;
			top: -2px;
			left: 2px;
			z-index: 10;
		}
	}
	&-make-name {
		display: block;
		margin-top: 5px;
		color: colors(basetxt);
		font-weight: 700;
		line-height: 1.3;
	}
	&-price {
		display: block;
		margin-top: 5px;
		color: colors(basetxt);
		font-weight: 700;
		line-height: 1.3;
	}
	&-name {
		display: block;
		margin-top: 5px;
		text-align: center;
		text-decoration: underline;
	}
}

//------------------------------------------------------------
// 
//------------------------------------------------------------

//=========================
// 
//=========================

.review {
	&-list-area {
		a {
			display: block;
			text-decoration: none;
			&:hover {
				@include block-link;
				& .ellipsis:before, & .ellipsis:after {
					background-color: rgb(240, 244, 254);
				}
			}
			.review-list-area-inner {
				border: 2px solid colors(col-lightgray);
				border-bottom: none;
				&.pickup-review {
					border: 4px solid colors(col-yellow);
				}
			}
			&:last-child .review-list-area-inner {
				border-bottom: 2px solid colors(col-lightgray);
			}
		}
		&-inner {
			padding: 18px;
			border: 2px solid colors(col-lightgray);
			border-bottom: none;
			@include fz(14);
			box-sizing: border-box;
			&:last-of-type {
				border-bottom: 2px solid colors(col-lightgray);
			}
			&.pickup-review {
				padding: 16px;
				border: 4px solid colors(col-yellow);
			}
		}
	}
	// 
	//------------------------------------------------------------
	&-supplement-info-block {
		// .mv-review-supplement-info-block, .mv-shop-outline-blockextend
		display: table;
		width: 100%;
		margin-bottom: 18px;
		padding-bottom: 18px;
		border-bottom: 1px dotted colors(col-lightgray);
		color: colors(basetxt);
		line-height: 1;
	}
	&-pickup-ico-box {
		@include table-cell;
		width: 116px;
		padding: 2px;
		background-color: colors(col-yellow);
		@include fz(12);
		font-weight: 700;
		text-align: center;
		text-shadow: 1px 1px 0px colors(col-white);
		vertical-align: middle;
		white-space: nowrap;
		box-sizing: border-box;
	}
	&-user-name-box {
		// .mv-review-user-name-boxextend
		@include table-cell;
		width: 65px;
		padding-left: 5px;
		padding-right: 5px;
		vertical-align: middle;
		&:first-child {
			padding-left: 0px;
		}
	}
	&-score-box {
		@include table-cell;
		position: relative;
		top: -0.1em;
		vertical-align: middle;
		.review-star {
			@extend %review-star;
			@include fz(20);
			vertical-align: text-bottom;
		}
	}
	&-days-box {
		// .mv-review-days-boxextend
		@include table-cell;
		width: 200px;
		text-align: right;
		vertical-align: middle;
	}
	// 
	//------------------------------------------------------------
	&-main-info-block {
		// .mv-review-main-info-block, .mv-shop-outline-blockextend
		display: table;
		width: 100%;
		table-layout: fixed;
	}
	&-spec {
		&-box {
			// .review-sell-price-box, .review-difference-box, .review-user-eval-box, .mv-review-spec-box, .mv-shop-outline-score-box, .mv-shop-outline-info-boxextend
			@include table-cell;
			padding-right: 20px;
			color: colors(basetxt);
			vertical-align: top;
			box-sizing: border-box;
			&:last-child {
				padding-right: 0;
			}
		}
		&-list {
			// .mv-review-spec-listextend
			display: table;
			margin-bottom: 10px;
			color: colors(basetxt);
			&:last-child {
				margin-bottom: 0;
			}
			&-ttl {
				// .mv-review-spec-list-ttlextend
				@include table-cell;
				width: 65px;
				padding-right: 5px;
				color: colors(col-blue);
				line-height: 1.25;
				font-weight: 700;
				vertical-align: top;
			}
			&-body {
				// .mv-review-spec-list-bodyextend
				@include table-cell;
				line-height: 1.25;
				vertical-align: top;
				word-wrap: break-word;
				overflow-wrap : break-word;
				a {
					&:link {
						color: colors(link);
						text-decoration: underline;
					}
					&.link-base-col {
						color: colors(basetxt);
						text-decoration: none;
						@include transition(0)
						&:hover {
							background-color: transparent;
							box-shadow: none;
						}
					}
				}
			}
		}
	}
	&-sell-price {
		&-box {
			@extend .review-spec-box;
			width: 150px;
			// 
			&.uservoice {
				width: 210px;
			}
		}
		&-list {
			margin-bottom: 10px;
			&-ttl {
				color: colors(col-blue);
				font-weight: 700;
				line-height: 1;
			}
			&-body {
				color: colors(col-red);
				font-weight: 700;
				line-height: 1.25;
			}
		}
	}
	&-difference {
		&-box {
			@extend .review-spec-box;
			width: 150px;
		}
		&-list {
			&-ttl {
				color: colors(col-blue);
				font-weight: 700;
				line-height: 1;
			}
			&-body {
				color: colors(col-red);
				font-weight: 700;
				line-height: 1.25;
				&-supplement {
					display: block;
					color: colors(basetxt);
					@include fz(12);
					font-weight: normal;
				}
			}
		}
	}
	&-user-eval {
		&-box {
			@extend .review-spec-box;
			width: 225px;
		}
		&-list {
			@extend .review-spec-list;
			&-ttl {
				@extend .review-spec-list-ttl;
				width: 100px;
				// 
				.uservoice & {
					width: 80px;
				}
			}
			&-body {
				@extend .review-spec-list-body;
				line-height: 1;
				.review-star {
					@extend %review-star;
					@include fz(18);
					vertical-align: text-bottom;
				}
			}
		}
	}
	&-photo {
		&-box {
			@include table-cell;
			width: 170px;
			padding-right: 20px;
			vertical-align: top;
			box-sizing: border-box;
			img {
				width: auto;
				max-width: 100%;
				height: auto;
			}
		}
	}
	&-assessment-price {
		&-box {
			@extend .review-spec-box;
			width: 300px;
			.review-difference-list-body-supplement {
				display: inline-block;
			}
		}
	}
	// 
	//------------------------------------------------------------
	&-outline-block {
		// .mv-review-outline-block, .mv-shop-outline-messageextend
		margin-top: 20px;
		padding: 5px 9px;
		border: 1px dotted colors(col-lightgray);
		color: colors(basetxt);
		& .ellipsis {
			position: relative;
			max-height: 72px;
			overflow: hidden;
			&:before,&:after {
				position: absolute;
				background: colors(col-white);
				@include transition(0.3s);
			}
			&:before {
				bottom: 0;
				right: 0;
				width: 1rem;
				padding: 0 0.5rem;
				content: "...";
			}
			&:after {
				width: 100%;
				height: 100%;
				content: "";
			}
		}
	}
	// 
	//------------------------------------------------------------
	&-detail-link-txt {
		margin-top: 10px;
		text-align: right;
		text-decoration: underline;
	}
}

//=========================
// 
//=========================

.market-simulate {
	&-area {
		padding: 20px;
		background-color: colors(col-xlightgray);
	}
	&-item {
		&-block {
			@include fz(0);
		}
		&-box {
			@include inline-block;
			width: 33%;
			margin-bottom: 20px;
			padding-right: 20px;
			@include fz(16);
			box-sizing: border-box;
			&:nth-child(3n) {
				width: 34%;
				padding-right: 0;
			}
			&-ttl {
				color: colors(col-blue);
				font-weight: 700;
			}
			&-body {
				select,
				input[type="text"] {
					width: 100%;
				}
			}
		}
	}
	&-search-btn {
		&-box {
			text-align: center;
			.btn-blue {
				padding-right: 40px;
				padding-left: 40px;
			}
		}
	}
	&-result {
		&-block {
			margin-top: 20px;
			padding: 10px;
			background-color: colors(col-white);
			@include fz(16);
			.module-before-txt {
				@include fz(16);
				font-weight: 700;
			}
			.assessment-module {
				&-block {
					margin-top: 0;
				}
				&-body {
					padding: 10px 80px;
					@include break-point(tbportrait) {
						padding: 10px;
					};
				}
			}
		}
		&-tbl {
			width: 100%;
			margin-bottom: 10px;
			caption {
				color: colors(col-blue);
				@include fz(18);
				font-weight: 700;
				text-align: center;
			}
			td, th {
				padding: 5px;
				border: 1px solid colors(col-lightgray);
				text-align: center;
				vertical-align: middle;
				&:first-child {
					width: 20%;
				}
			}
			th {
				background-color: colors(col-blue);
				color: colors(col-white);
			}
		}
	}
}

//------------------------------------------------------------
// 
//------------------------------------------------------------

// review-list-area

.mv-review {
	&-list-area {
		margin-bottom: 10px;
		border: 1px solid colors(col-lightgray);
		background-color: colors(col-white);
		&-ttl {
			@include padding(20px 20px null 20px)
			.ttl-block {
				margin-bottom: 0;
				line-height: 1;
			}
		}
	}
	// 
	//------------------------------------------------------------
	&-supplement-info-block {
		@extend .review-supplement-info-block;
		padding: 20px;
		margin-bottom: 0;
		box-sizing: border-box;
	}
	&-user-name-box {
		@extend .review-user-name-box;
		width: auto;
	}
	&-days-box {
		@extend .review-days-box;
		width: auto;
	}
	// 
	//------------------------------------------------------------
	&-main-info-block {
		// .mv-shop-outline-blockextend
		@extend .review-main-info-block;
		padding: 20px;
		box-sizing: border-box;
	}
	&-spec {
		&-box {
			// .mv-shop-outline-score-box, .mv-shop-outline-info-boxextend
			@extend .review-spec-box;
			width: 360px;
			box-sizing: border-box;
			@include break-point(tbportrait) {
				display: block;
				width: auto;
				padding-right: 0;
			}
			figure {
				// .mv-shop-outline-score-box figureextend
				@include inline-block;
				position: relative;
				width: 100%;
				height: 190px;
				margin-bottom: 20px;
				padding: 10px;
				border: 1px solid colors(col-lightgray);
				overflow: hidden;
				vertical-align: bottom;
				box-sizing: border-box;
				&:last-child {
					margin-bottom: 0;
				}
				img {
					// .mv-shop-outline-score-box figure imgextend
					position: relative;
					top: 50%;
					left: 50%;
					width: auto;
					max-width: 100%;
					height: auto;
					max-height: 100%;
					@include translate(-50%,-50%);
				}
			}
		}
		&-list {
			@extend .review-spec-list;
			&.satisfy {
				margin-bottom: 10px;
			}
			&-ttl {
				@extend .review-spec-list-ttl;
				width: 110px;
				.satisfy & {
					background: colors(col-red);
					color: colors(col-white);
					@include fz(20);
					font-weight: 700;
					line-height: 1;
					text-align: center;
					vertical-align: middle;
				}
			}
			&-body {
				@extend .review-spec-list-body;
				padding-left: 10px;
				.satisfy & {
					@include fz(28);
					line-height: 1;
				}
				.review-star {
					@extend %review-star;
				}
			}
		}
	}
	&-eval {
		&-box {
			// .pickup-review-list-eval-boxextend
			@include table-cell;
			@include break-point(tbportrait) {
				display: block;
			};
			&-ttl {
				// .pickup-review-list-eval-box-ttlextend
				color: colors(ttl);
				@include fz(16);
				font-weight: 700;
				text-align: left;
			}
			&-body {
				// .pickup-review-list-eval-box-bodyextend
				display: table;
				width: 100%;
				margin-bottom: 20px;
				border: 1px solid colors(col-blue);
				box-sizing: border-box;
				table-layout: fixed;
				.price-group {
					@include table-cell;
					width: 480px;
					@include break-point(tbportrait) {
						display: block;
						width: 100%;
					};
					&-tbl {
						@include break-point(tbportrait) {
							width: 100%;
						};
						tr {
							&:nth-child(2n) {
								border-top: 1px solid colors(col-blue);
							}
						}
						td {
							width: 120px;
							border-right: 1px solid colors(col-blue);
							vertical-align: middle;
							@include break-point(tbportrait) {
								width: 25%;
							};
							&:nth-child(4n) {
								border-right: none;
							}
							.ico-rank {
								display: block;
							}
							.txt-price {
								display: block;
								padding: 0 5px;
								color: colors(col-red);
								font-weight: 700;
								text-align: right;
							}
						}
					}
				}
				.difference-group {
					@include table-cell;
					border-left: 1px solid colors(col-blue);
					text-align: center;
					vertical-align: middle;
					@include break-point(tbportrait) {
						display: block;
						border-top: 1px solid colors(col-blue);
						border-left: none;
					};
					.difference-txt {
						display: block;
						@include fz(16);
						@include break-point(tbportrait) {
							@include inline-block;
						};
					}
				}
			}
		}
	}
	&-assessment-info {
		&-box {
			@include table-cell;
			vertical-align: top;
			@include break-point(tbportrait) {
				display: table;
				width: 100%;
				margin-top: 20px;
				table-layout: fixed;
			}
		}
		&-group {
			@include inline-block;
			margin-right: 40px;
			vertical-align: top;
			&:last-of-type {
				margin-right: 0;
			}
			@include break-point(tbportrait) {
				@include table-cell;
			}
		}
		&-list {
			@extend .review-spec-list;
			margin-bottom: 20px;
			&-ttl {
				@extend .review-spec-list-ttl;
				display: block;
				width: 100%;
			}
			&-body {
				@extend .review-spec-list-body;
				display: block;
				.review-star {
					@extend %review-star;
					@include fz(24);
					vertical-align: middle;
				}
			}
		}
	}
	// 
	//------------------------------------------------------------
	&-outline-block {
		// .mv-shop-outline-messageextend
		@extend .review-outline-block;
		margin-top: 0;
		padding: 15px;
	}
	// 
	//------------------------------------------------------------
	&-sell-point-block {
		@include padding(null 20px 20px 20px);
		box-sizing: border-box;
	}
}