// Styleguide 2.0.0

//------------------------------------------------------------
// icons
//------------------------------------------------------------

//=========================
// 
//=========================

// 
//------------------------------------------------------------

.ico-new {
	@include inline-block;
	margin: 0 0 0 2px;
	padding: 2px 5px;
	background-color: colors(col-red);
	color: colors(col-white);
	@include fz(10);
	line-height: 1;
	vertical-align: middle;
	white-space: nowrap;
}

.ico-external {
	@include inline-block;
	@include margin(null 5px null 5px);
	vertical-align: baseline;
}

// 
//------------------------------------------------------------

.ico-arrow {
	color: colors(col-gray);
	text-decoration: none;
	vertical-align: middle;
}

.ico-arw-wedge {
	@include inline-block;
	padding-left: 15px;
	position: relative;
	&:after {
		@include arw-wedge;
		top: 0.75em;
		left: 0;
		content: "";
	}
}

//=========================
// 
//=========================

@for $value from 1 through 10 {
	.ico-size-#{$value * 1} {
		@include scale($value * 0.1, $value * 0.1);
	}
}

//=========================
// 
//=========================

@font-face {
	font-family: 'kcvicon';
	src:
		url('/img/fonts/kcvicon.ttf?w4umya') format('truetype'),
		url('/img/fonts/kcvicon.woff?w4umya') format('woff'),
		url('/img/fonts/kcvicon.svg?w4umya#kcvicon') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="ficon-"], [class*=" ficon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	display: inline-block;
	font-family: 'kcvicon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

